.center-layout {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffffff;
  overflow-y: auto;
}

.container {
  width: 100vw;
  height: calc(100vw * (19.5 / 9));
  max-height: 100vh;
  max-width: calc(100vh * (9 / 19.5));

  padding-left: calc(100vw * 0.05);
  padding-right: calc(100vw * 0.05);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  background-color: #ffffff;
}

.start-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.ibm-plex-sans-thai-thin {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: 100;
  font-style: normal;
}

.ibm-plex-sans-thai-extralight {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: 200;
  font-style: normal;
}

.ibm-plex-sans-thai-light {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: 300;
  font-style: normal;
}

.ibm-plex-sans-thai-regular {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.ibm-plex-sans-thai-medium {
