img {
  max-height: 30vh;
}

h1 {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: bold;
  color:#172A3A;
  text-align: center;
  font-size: 2.5rem;
}

p {
  font-family: "IBM Plex Sans Thai", sans-serif;
  color:#172A3A;
  text-align: center;
  font-size: 1rem;
}

button {
  font-family: "IBM Plex Sans Thai", sans-serif;
  font-weight: bold;
  color: #ffffff;
  background-color: #31347C;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.8rem;
  margin-top: 20px;
  width: 100%;
}

button:hover {
  background-color: #508AB9;
  transition-duration: 30ms;
}